<template>
  <div style="height: 100vh; width: 100vw">
    <!-- Header -->
    <div style="padding: 80px 0 30px">
      <div class="d-flex justify-center">
        <img width="64" height="64" src="/favicon.png">
        <h1 class="my-auto ml-2" style="font-size: 40px;">AdapTest</h1>
      </div>
      <h3 class="mt-4">Адаптивное тестирование - сервис помогающий быстро и удобно определить уровень подготовки</h3>
    </div>
    <v-subheader inset style="font-size: 16px">
      Категории → Программирование → ООП
    </v-subheader>
    <v-divider/>
    <!--  Main  -->
    <v-container class="mt-4">
      <v-stepper
          v-model="level"
          width="100%"
          height="100%"
      >
        <!--  Levels   -->
        <v-stepper-header>
          <v-stepper-step step="1"
          >
            Лёгкий уровень знаний
          </v-stepper-step>

          <v-divider></v-divider>
          <v-stepper-step step="2"
          >
            Средний уровень знаний
          </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="3">
            Высокий уровень знаний
          </v-stepper-step>
        </v-stepper-header>
        <!--  Question -->
        <div class="ma-8 mb-0">
          <div v-if="currentQuestion" class="d-flex flex-column align-baseline mt-4">
            <!-- Type: singleradiobutton -->
            <template v-if="currentQuestion.type === 'singleradiobutton'">
              <p v-text="currentQuestion.text" class="mb-0"/>
              <!-- if have img -->
              <v-img v-if="currentQuestion.img" :src="currentQuestion.img" style="border-radius: 8px; margin-top: 16px"></v-img>
              <v-radio-group v-model="answer">
                <v-radio
                    v-for="(n, index) in currentQuestion.variants"
                    :key="n"
                    :label="n"
                    :value="index"
                ></v-radio>
              </v-radio-group>
            </template>
            <!-- Type: multiradiobutton -->
            <template v-else-if="currentQuestion.type === 'multiradiobutton'">
              <p v-text="currentQuestion.text" class="mb-0"/>
              <v-radio-group v-model="answer" multiple>
                <v-radio
                    v-for="(n, index) in currentQuestion.variants"
                    :key="n"
                    :label="n"
                    :value="index"
                ></v-radio>
              </v-radio-group>
            </template>
            <!-- Type: text -->
            <template v-else-if="currentQuestion.type === 'text'">
              <p v-text="currentQuestion.text"/>
              <v-textarea v-model.trim="answer" filled auto-grow style="width: 100%"></v-textarea>
            </template>
            <!-- Type: relation -->
            <template v-else-if="currentQuestion.type === 'relation'">
              <p class="font-weight-bold">Установите соответствия:</p>
              <div class="d-flex my-4" style="width: 100%">
                <div class="d-flex flex-column align-baseline flex-grow-1">
                  <p v-text="currentQuestion.title1" class="mb-6 text-decoration-underline"/>
                  <div class="d-flex align-center my-2" v-for="(el, index) in currentQuestion.variants1" :key="index">
                    <p v-text="el" class="mb-0"/>
                    <v-select
                        dense
                        hide-details
                        style="width: 70px"
                        v-model="answer[index]"
                        :items="(Array.apply(null, {length: currentQuestion.variants2.length}).map(Number.call, Number)).map(el => el + 1)"
                        outlined
                        class="ml-4"
                    />
                  </div>
                </div>
                <div class="d-flex flex-column align-baseline flex-grow-1">
                  <p v-text="currentQuestion.title2" class="mb-6 text-decoration-underline"/>
                  <div v-for="el in currentQuestion.variants2" :key="el">
                    <p v-text="el" class="my-3"/>
                  </div>
                </div>
              </div>
            </template>
          </div>
        </div>
        <div class="d-flex justify-end">
          <v-btn
              :disabled="answer === null"
              color="primary"
              class="mr-4"
              rounded
              outlined
              @click="clear()"
          >
            Очистить
          </v-btn>
          <v-btn
              :disabled="!answer || (Array.isArray(answer) && answer.includes(undefined))"
              color="primary"
              class="mb-6 mr-8"
              rounded
              @click="confirm()"
          >
            Подтвердить
          </v-btn>
        </div>
      </v-stepper>
    </v-container>
    <v-snackbar
        timeout="600"
        v-model="showSnackbar"
        color="primary"
        text
    >
      <div style="font-size: 18px">Ответ принят.</div>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  name: 'Test',
  data() {
    return {
      /* Config */
      level: 2,
      maxRepeat: 3,
      needPointForNextLvl: 2,

      levelRepeat: [null, 0, 0, 0],
      currentPointForNextLvl: 0,
      currentQuestion: null,
      questions: [
        // lvl 1
        {
          level: 1,
          type: "singleradiobutton",
          text: "Как называется способность объекта скрывать свои данные и реализацию от других объектов системы?",
          variants: [
            "Полиморфизм",
            "Инкапсуляция",
            "Абстракция",
            "Наследование"
          ],
          trueAnswerNumber: 1
        },
        {
          level: 1,
          type: "singleradiobutton",
          text: "Выберите правильную структуру альтернативного условного оператора (полный вариант)",
          variants: [
            "If <условие> Then <оператор 1>",
            "If <условие> Then <оператор 1> Else <оператор 2>",
            "If <условие> Then <оператор 1> <оператор 2>",
            "If <условие 1> Then if <условие 2> Then <оператор 1> <оператор 2>"
          ],
          trueAnswerNumber: 1
        },
        {
          level: 1,
          type: "singleradiobutton",
          text: "Какой принцип нарушает следующий код:",
          img: "assets/1-1.png",
          variants: [
            "Инкапсуляция",
            "Композиция",
            "Равенство",
            "Полиморфизм",
            "Агрегация"
          ],
          trueAnswerNumber: 0
        },
        // lvl 2
        {
          level: 2,
          type: "multiradiobutton",
          text: "Что из перечисленного относится к трём китам ООП (три основные понятия)?",
          variants: [
            "Модульность",
            "Наследование",
            "Однозначность",
            "Полиморфизм",
            "Разделение обязанностей",
            "Строгая типизация",
            "Инкапсуляция"
          ],
          trueAnswerNumber: [1, 3, 6]
        },
        {
          level: 2,
          type: "multiradiobutton",
          text: "Какие бывают виды наследования?",
          variants: [
            "Единственное наследование",
            "Множественное наследование",
            "Многоуровневое наследование",
            "Иерархическое наследование",
            "Гибридное наследование",
            "Полное наследование",
            "Полиморфизм"
          ],
          trueAnswerNumber: [1, 2, 3, 4]
        },
        // lvl 3
        {
          level: 3,
          type: "relation",
          title1: "Определение",
          variants1: [
            "A. Использует переменную, которая последовательно принимает значения из заданного диапазона. С каждой сменой значения переменной выполняются действия, заключенные в теле цикла",
            "B. Выполняет набор действий для каждого объекта из указанной группы объектов",
            "C. Выполняет блок кода до тех пор, пока выполняется заданное условие"
          ],
          title2: "Название цикла",
          variants2: [
            "1. For Each",
            "2. For...Next",
            "3. Do While"
          ],
          trueAnswer: [1, 2, 3]
        },
        {
          level: 3,
          type: "text",
          text: "Как называется разновидность управляющей конструкций предназначенная для организации многократного исполнения набора инструкций?",
          trueAnswerText: "Цикл"
        },
        {
          level: 3,
          type: "relation",
          title1: "Свойство",
          variants1: [
            "A. Сущность реального мира",
            "B. Работает как переменная класса",
            "C. Это физическая сущность",
            "D. Занимает место в памяти при создании",
            "E. Должен быть объявлен каждый раз, когда нужен",
            "F. Обычно является шаблоном создания объектов",
            "G. Объединяет методы и данные в единую ячейку",
            "H. Это логическая сущность",
            "I. Не занимает место в памяти при создании",
            "J. Объявляется один раз",
          ],
          title2: "Название",
          variants2: [
            "1. Объект",
            "2. Класс"
          ],
          trueAnswer: [1, 1, 1, 1, 2, 2, 2, 2, 2, 2]
        },
      ],
      answer: null,

      /* Layout */
      showSnackbar: false,
    }
  },
  methods: {
    nextQuestion() {
      let currentLvlQuestions = this.questions.filter(el => el.level === this.level && JSON.stringify(this.currentQuestion) !== JSON.stringify(el));
      this.currentQuestion = currentLvlQuestions.rand();
      this.answer = null;
    },
    confirm() {
      this.showSnackbar = true;

      let answerIsTrue = false;
      if (this.currentQuestion.type === "text" && this.currentQuestion.trueAnswerText === this.answer) answerIsTrue = true;
      if (this.currentQuestion.type === "singleradiobutton" && this.currentQuestion.trueAnswerNumber === this.answer) answerIsTrue = true;
      if (this.currentQuestion.type === "multiradiobutton" && this.currentQuestion.trueAnswerNumber.length === this.answer.length && JSON.stringify(this.answer.sort((a, b) => a - b)) === JSON.stringify(this.currentQuestion.trueAnswerNumber.sort((a, b) => a - b))) answerIsTrue = true;
      if (this.currentQuestion.type === "relation" && JSON.stringify(this.answer) === JSON.stringify(this.currentQuestion.trueAnswer)) answerIsTrue = true;

      if (answerIsTrue) {
        this.currentPointForNextLvl++;

        if (this.currentPointForNextLvl === this.needPointForNextLvl) {
          this.currentPointForNextLvl = 0;
          this.level++;
          this.levelRepeat[this.level]++;
        }
      } else {
        this.currentPointForNextLvl--;

        if (Math.abs(this.currentPointForNextLvl) === this.needPointForNextLvl) {
          this.currentPointForNextLvl = 0;
          this.level--;
          this.levelRepeat[this.level]++;
        }
      }

      /* Check test end */
      /* Repeat */
      if (this.level === 1 && this.maxRepeat === this.levelRepeat[this.level]) this.$router.push({ name: 'result', params: { lvl: "1" } });
      if (this.level === 2 && this.maxRepeat === this.levelRepeat[this.level]) this.$router.push({ name: 'result', params: { lvl: "2" } });
      if (this.level === 3 && this.maxRepeat === this.levelRepeat[this.level]) this.$router.push({ name: 'result', params: { lvl: "3" } });
      /* Out */
      if (this.level === 0) this.$router.push({ name: 'result', params: { lvl: "1" } });
      if (this.level === 4) this.$router.push({ name: 'result', params: { lvl: "3" } });
      this.nextQuestion();
    },
    clear() {
      this.currentQuestion.type === "relation" ? this.answer = new Array(this.currentQuestion.variants1.length) : this.answer = null
    }
  },
  watch: {
    currentQuestion(val) {
      if (val.type === "relation") this.answer = new Array(this.currentQuestion.variants1.length)
    }
  },
  mounted() {
    // Функция получение случайного элемента массива
    Array.prototype.rand = function () {
      return this[Math.floor(Math.random() * this.length)];
    }
    // Устанавливаем начальный вопрос
    this.nextQuestion();
  }
}
</script>

